/**
 * GIBIへの視聴通知
 */
'use strict';

class MovizoReporter {

  constructor(player, accountID, movieID, fileName, options) {
    this.accountID = accountID;
    this.movieID = movieID;
    this.fileName = fileName;
    this.player = player;
    this.options = options;
    this.started = false;
    this.playing = false;
    this.deviceID = this.loadDeviceID();

    this.startTime = null;  // 通知開始位置
    this.endTime = null;  // 通知終了位置
    this.tmpEndTime = null; // 仮の通知終了位置

    // FF では seek したとき、play は来なくて playing だけが来たことがあった。
    // どっちでもいいことにしておく。
    //
    // Android 2.2 (INFOBAR A01) では bind が使えなかったので、handleEvent 方式でやる。
    // https://developer.mozilla.org/ja/docs/Web/API/EventTarget/addEventListener
    /*
    this.player.addEventListener('play', this.onPlay.bind(this));
    this.player.addEventListener('playing', this.onPlay.bind(this));
    this.player.addEventListener('pause', this.onPause.bind(this));
    this.player.addEventListener('ended', this.onEnded.bind(this));
    this.player.addEventListener('timeupdate', this.onTimeUpdate.bind(this));
    */
    this.player.addEventListener('play', this, false);
    this.player.addEventListener('playing', this, false);
    this.player.addEventListener('pause', this, false);
    this.player.addEventListener('ended', this, false);
    this.player.addEventListener('timeupdate', this, false);
    this.player.addEventListener('seeking', this, false);
    this.player.addEventListener('seeked', this, false);
    window.addEventListener('beforeunload', this, false);

    this.handleEvent = function(event) {
      switch (event.type) {
        case 'play':
        case 'playing':
          this.onPlay();
          break;
        case 'pause':
          this.onPause();
          break;
        case 'ended':
          this.onEnded();
          break;
        case 'seeking':
          this.onSeeking();
          break;
        case 'seeked':
          this.onSeeked();
          break;
        case 'timeupdate':
          this.onTimeUpdate();
          break;
        // ページ移動
        case 'beforeunload':
          this.onBeforeUnload();
          break;
      }
    };
  }

  destroy() {
    window.removeEventListener('beforeunload', this, false);
  }

  onPlay() {
    this.logTimes('onPlay:enter');

    if (this.started) {
      // 保持していた期間を通知
      this.sendDelta('play');
    }
    else {
      // 再生開始を通知
      this.started = true;
      this.sendReport('start', null);
    }

    this.behaveAsPlay();

    this.playing = true;

    this.logTimes('onPlay:return');
  }

  behaveAsPlay() {
    // 開始位置をリセット
    this.startTime = this.endTime = this.tmpEndTime = this.player.currentTime;
  }

  onPause() {
    this.logTimes('onPause:enter');

    this.playing = false;

    // pause が来るときは、こんなとき
    //
    // 1. ユーザ操作による一時停止
    //    このあと再開すれば play/playing が来る。
    //
    // 2. ユーザ操作によるseek(再生中でも)
    //    このあとseek位置に来れば同様に play/playing が来る
    //
    // 3. 動画終了位置まで到達
    //    このあとendedが来る。
    //
    this.behaveAsPause('pause');

    this.logTimes('onPause:return');
  }

  behaveAsPause(reason) {
    // onPause に書いた 2 のように seek すると、seek先の位置で一旦 pause が来るので
    // そこを終了位置としてはいけません。
    // ユーザ操作の pause ならその前に timeupdate が来ているので、すでに endTime は更新されている。
    // ただし、その前に timeupdate が来ているのならその位置を使う。
    //this.endTime = this.player.currentTime;
    if (this.tmpEndTime != null) {
      this.endTime = this.tmpEndTime;
    }

    // 保持していた期間を通知
    this.sendDelta(reason, true);

    // 1, 2 のときは onPlay で startTime 等がリセットされるので
    // ここで null にする必要が無いと思うだろうが、実は Android (Nexus5) Chrome では
    // PAUSE状態で seek のためにプログレスバーをスライドさせると、timeupdate でどんどん
    // やってくるので、ここで null にしておかないと古い startTime 等が残ってしまう。
    this.startTime = this.endTime = this.tmpEndTime = null;
  }

  onBeforeUnload() {
    // 停止中じゃないなら送信する
    if (this.playing) {
      this.behaveAsPause('beforeunload');
    }
  }

  onEnded() {
    this.logTimes('onEnded:enter');

    // これの前には pause がやってきているはずなので、sendDelta しても実は送られない。
    //
    // これは FireFox での例
    //
    // timeupdate @ 293.867664
    // timeupdate @ 294.0343
    // >>> REPORT: 291 .. 294.0343
    // pause @ 294.0343
    // timeupdate @ 294.0343
    // ended @ 294.0343
    //

  // - Mac の Safari で HLS を再生したとき、最後まで再生して ended がやってくるのだが、そのあと rewind すると,
  //    また ended がやってくるが、そのときの currentTime は 0
  // - Android 2.3 では 最後まで再生すると勝手に rewind されて timeupdate が 0秒位置でやってくるので、そっちで対応する
  // - でも Win10 + IE11 では、ended の前には timeupdate が続くだけなので、ここで送信しないと、もうできない。
    
    // IE11で
    // duration    = 283.6062666 なのに
    // currentTime = 283.6291647 と、duration より大きいことがあった。
    if (Math.abs(this.player.currentTime - this.player.duration) < 0.1) {
      this.endTime = this.player.duration;
      this.sendDelta('ended', true);
    }

    this.startTime = this.endTime = this.tmpEndTime = null;

    this.logTimes('onEnded:return');
  }

  onSeeking() {
    this.logTimes('onSeeking:enter');

    // Desktop では 再生中に seek すると、一旦 pause が来るのだが
    // Android (Nexus5) ではいきなり seeking が来るので、ここで pause と同様の操作をする。
    this.behaveAsPause('seeking');

    this.logTimes('onSeeking:return');
  }

  onSeeked() {
    this.logTimes('onSeeked:enter');
    
    // Desktop では 再生中に seek すると、一旦 pause が来て、再生再開時に play が来るのだが
    // Android (Nexus5) ではいきなり seeking が来て、seek 完了後に seeked が来るので
    // ここでは play と同様の動作をする。
    this.behaveAsPlay();

    this.logTimes('onSeeked:return');
  }

  onTimeUpdate() {
    this.logTimes('onTimeUpdate:enter');

    // Desktop(FireFox)ではPAUSE中のseekではtimeupdateが来ないが
    // Android(Nexus5) Chrome ではやってきた。
    if (this.playing) {

      if (this.tmpEndTime != null) {
        let delta = Math.abs(this.player.currentTime - this.tmpEndTime);

        // Android 2.3 (INFOBAT A01) では、再生中に seek すると、pause/play どころか
        // Android 4.4 (Nexus5) ではやってきていた seeking/seek もやって来ない。
        // 通常の再生では timeupdate が１秒以上開けてやってくることは無いので
        // そんなときは pause/play が間に入っているものとして扱うことにした。 
        if (delta > 1.0) {
          this.behaveAsPause('timeupdate');
          this.behaveAsPlay();
        }

        this.endTime = this.tmpEndTime;
      }
      this.tmpEndTime = this.player.currentTime;

      if (this.startTime == null) {
        this.startTime = this.tmpEndTime;
      }
      else if (this.endTime != null) {
        let delta = this.endTime - this.startTime;
        if (delta >= this.options.reportDelta) {
          this.sendDelta('timeupdate');
        }
      }
    }

    this.logTimes('onTimeUpdate:return');
  }

  sendDelta(reason, force = false) {
    if (this.startTime == null || this.endTime == null) {
      return;
    }

    var minDelta = (force ? 0.001 : 1.0);
    if (this.endTime - this.startTime < minDelta) {
      return;
    }

    this.sendReport('play', {
      duration: this.player.duration,
      start: this.startTime,
      end: this.endTime,
      reason: reason
    });

    this.startTime = this.endTime;
    this.endTime = null;
  }

  sendReport(state, params) {
    if (params == null) {
      params = {};
    }

    params.accountID = this.accountID;
    params.movieID = this.movieID;
    params.filename = this.fileName;
    params.device = this.deviceID;

    var query = `state=${state}`;
    for (var key in params) {
      var value = params[key];
      query += `&${key}=${value}`;
    }

    var url = `${this.options.reportURL}?${query}`;
    var xhr = this.loader = new XMLHttpRequest();
    xhr.open('GET', url, true);
    xhr.send();
    
    if (this.options.reportCallback != null) {
      params.state = state;
      this.options.reportCallback(params);
    }
  }

  loadDeviceID() {
    var KEY_ID = 'GIBI-LOG-DEVICE-ID';
    var deviceID = localStorage.getItem(KEY_ID);
    if (deviceID == null) {
      deviceID = this.genDeviceID();
      localStorage.setItem(KEY_ID, deviceID);
    }
    return deviceID;
  }

  genDeviceID() {
    var length = 16;
    // 生成する文字列に含める文字セット
    var chars = 'abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
    var charsLen = chars.length;
    var text = '';
    for(var i = 0; i < length; i++){
      text += chars[Math.floor(Math.random()*charsLen)];
    }
    return text;
  }

  logTimes(from) {
    // この無意味な return は jshint の `'from' is defined but never used.` のエラーを避けるため
    //console.log(`${from}: playing=${this.playing}, start=${this.startTime}, end=${this.endTime}, tmp=${this.tmpEndTime}`);
    return from;
  }
}

MovizoReporter.options = {
  poster: "naha"
};

export default MovizoReporter;
