import { Logger } from '../utils/logger'
const MAX_ITEMS = 10

/**
 * 同じURLなら何度もリクエストしないようにキャッシュする
 */
export class FragmentCache {

    constructor({ debug }) {
        this.queue = []
        this.cache = {}
        this.logger = new Logger(debug)
        this.disabled = false
    }

    get(context) {
        if (this.disabled) return

        const item = this.cache[context.url]
        if (!item) {
            this.logger.log(`FragmentCache.get: cache miss: url=${context.url}`)
            return
        }
        this.logger.log(`FragmentCache.get: cache HIT: url=${context.url}`)
        const [loaderResponse, stats, response] = item
        return [loaderResponse, stats, context, response]
    }

    put(loaderResponse, stats, context, response) {
        if (this.disabled) return

        this.logger.log(`FragmentCache.put: url=${context.url}`)
        this.cache[context.url] = [loaderResponse, stats, response]
        this.queue.push(context.url)
        while (this.queue > MAX_ITEMS) {
            const url = this.queue.shift()
            delete this.cache[url]
            this.logger.log(`FragmentCache.put: DROP url=${url}`)
        }
    }
}