/**
 * Browser Specs
 */
'use strict';

class BrowserSpec {

  static videoSupports(contentType) {
    var video = document.createElement('video');
    switch (video.canPlayType(contentType)) {
      case 'probably':
      case 'maybe':
        return true;
      default:
        return false;
    }
  }

  static get isSafari() {
    const ua = navigator.userAgent;
    if (ua.indexOf(' Chrome/') !== -1) return false;
    if (ua.indexOf(' Edg/') !== -1) return false;
    // Safari/ は Chrome, Edge でも UA に含まれている。
    if (ua.indexOf(' Safari/') !== -1) return true;
    return false;
  }

  static get iosVersion() {
    var ua = navigator.userAgent;
    if (ua.search(/iPhone/) !== -1 || ua.search(/iPad/) !== -1) {
      var version = this.getVersion(ua, /OS (\d+_*\d*).*? like Mac/);
      return version.replace(/_/g,'.');
    }
    return null;
  }

  static get androidVersion() {
    var ua = navigator.userAgent;
    if (ua.search(/Android/) !== -1) {
      return this.getVersion(ua, /Android (.*?);/);
    }
    return null;
  }

  // Android Chrome の UA
  // Mozilla/5.0 (Linux; Android 6.0; Nexus 5 Build/MRA58N) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/52.0.2725.0 Mobile Safari/537.36
  static get androidChromeVersion() {
    var ua = navigator.userAgent;
    if (ua.search(/Android/) !== -1) {
      return this.getVersion(ua, /Chrome\/(\d+)/);
    }
    return null;
  }

  // Windows 10 Edge の UA
  // Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/51.0.2704.79 Safari/537.36 Edge/14.14393
  static get edgeVersion() {
    var ua = navigator.userAgent;
    return this.getVersion(ua, / Edge (\d+\.\d+)/);
  }

  static getVersion(ua, pattern) {
    var match = ua.match(pattern);
    if (match && match[1]) {
      return match[1];
    }
    return null;
  }
}

export default BrowserSpec;
