export class LoadStats {

    constructor() {
        this.aborted /*: boolean */ = false;
        this.loaded /*: number */ = 0;
        this.retry /*: number */ = 0;
        this.total /*: number */ = 0;
        this.chunkCount /*: number */ = 0;
        this.bwEstimate /*: number */ = 0;
        this.loading /*: HlsProgressivePerformanceTiming */ = { start: 0, first: 0, end: 0 };
        this.parsing /*: HlsPerformanceTiming */ = { start: 0, end: 0 };
        this.buffering /*: HlsProgressivePerformanceTiming */ = { start: 0, first: 0, end: 0 };
    }
}
