import {writeInlinePlayerStyleSheet} from "./player-controls.css.js";

class InlinePlayerControls {

    static _addStyleSheet() {
        if (InlinePlayerControls._styleSheetAdded)
            return;

        writeInlinePlayerStyleSheet();
        InlinePlayerControls._styleSheetAdded = true;
    }

    constructor(player, playerElement, fullscreenElement) {

        InlinePlayerControls._addStyleSheet();
        this.playerElement = playerElement;
        this.fullscreenElement = fullscreenElement;


        // http://phpspot.org/blog/archives/2007/04/javascriptie1.html
        // IEかどうかの判定はこれだけでOKらしい
        // ...ということだったが、IE10までしか対応しないのか、IE11ではfalseになった。
        //var isMSIE = /*@cc_on!@*/false;
        this.isMSIE = (navigator.userAgent.toLowerCase().indexOf("trident/7") != -1);

        this.elements = {};
        this.elements.panel = this._appendDiv("-movizo-control-panel", playerElement);
        this.elements.play = this._appendDiv(["-movizo-play-button", "-movizo-button", "-movizo-icon-play"], this.elements.panel);
        this.elements.timeElapsed = this._appendDiv(["-movizo-time-elapsed", "-movizo-time"], this.elements.panel);
        this.elements.progress = this._appendDiv("-movizo-progress", this.elements.panel);
        this.elements.seekBar = this._appendDiv("-movizo-seek-bar", this.elements.progress);
        this.elements.seekElapsed = this._appendDiv("-movizo-seek-elapsed", this.elements.seekBar);
        this.elements.seekThumb = this._appendDiv("-movizo-seek-thumb", this.elements.progress);
        this.elements.timeRemaining = this._appendDiv(["-movizo-time-remaining", "-movizo-time"], this.elements.panel);
        this.elements.mute = this._appendDiv(["-movizo-mute-button", "-movizo-button", "-movizo-icon-unmute"], this.elements.panel);
        this.elements.volume = this._appendDiv(["-movizo-volume", "-movizo-icon-volume8"], this.elements.panel);
        if (this.canFullscreen)
            this.elements.zoom = this._appendDiv(["-movizo-zoom-button", "-movizo-button", "-movizo-icon-zoom"], this.elements.panel);
        this.elements.placeHolder = this._appendDiv("-movizo-placeHolder", this.elements.panel);
        this.elements.centerHolder = this._appendDiv("-movizo-center-holder", playerElement);

        this.elements.spinner = this._appendDiv(["-movizo-spinner", "-movizo-icon-spinner"], playerElement);
        this.elements.rewind = this._appendDiv(["-movizo-rewind", "-movizo-icon-play"], playerElement);

        this.elements.skipWrapper = this._appendDiv(["-movizo-skip-wrapper"], this.elements.panel);
        this.elements.skipPanel = this._appendDiv(["-movizo-skip-panel"], this.elements.skipWrapper);
        this.elements.skipBack = this._appendDiv(["-movizo-skip", "-movizo-skip-back"], this.elements.skipPanel);
        this.elements.skipFore = this._appendDiv(["-movizo-skip", "-movizo-skip-fore"], this.elements.skipPanel);

        this.thumbSeeking = false;
        this.hideTimer = null;
        this.player = player;

        this.scaledStyles = null;
        this.fullStyles = null;
        this.showRewindAfterSpinner = false;

        var self = this;
        player.addEventListener("loadedmetadata", function(){
            self._updateTimes();
            self._updateState("loadedmetadata");
        });
        player.addEventListener("timeupdate", function(){ self._onTimeupdate(); });
        player.addEventListener("play", function(){ self._updateState("play"); });
        player.addEventListener("pause", function(){ self._updateState("pause"); });
        player.addEventListener("volumechange", function(){ self._updateState("volumechange"); });

        // 初期状態では隠しておく
        this.hidePanel();
        this._addMouseEvents();

        this.autoplayTimer = null;
    }

    // コントロールを表示することになったので、初期化
    activate(options) {
        var self = this;

        // this.showPanel();
        if (options.autoplay) {
            self.player.addEventListener("loadeddata", function(e) {
                self.autoplayTimer = setTimeout(function() {
                    self.showRewindAfterSpinner = true;
                    self._showRewind(true, "play");
                    self.showPanel();
                }, 500);
            });
            self.player.addEventListener("timeupdate", function(e) {
                if (self.autoplayTimer) {
                    clearTimeout (self.autoplayTimer);
                    self.autoplayTimer = null;

                    self.showRewindAfterSpinner = false;
                    self._showRewind(false, "play");
                    self.hidePanel();
                }
            });
        }
        else {
            // autoplayならパネル表示
            this.showPanel();
        }

        // loopでrewindするときにも、表示されてしまうが、区別するのが大変なので
        // とりあえず表示されてもいいことにする。
        this.player.addEventListener("pause", function(){ self._showControlsPanel("pause"); });
        //this.player.addEventListener("play", function(){ self._showControlsPanel("play", /*hideAfterwards*/true); });

        // play中にseekされた時は、コントロールパネルを表示しておく。
        // これをやらないと、上のplayで呼んだdelayで消えてしまう。
        this.player.addEventListener("seeking", function(){ self._showControlsPanel("seeking", /*hideAfterwards*/true); });
        // fullscreen 状態が変化したらコントロールパネルの最大化/最小化ボタンを切り替えるためにントロールパネルを再表示する。
        // PCのESCや、Androidの戻る機能でフルスクリーンを解除した時に、ボタンが最小化のままにならないように。
        document.addEventListener("fullscreenchange", function(){ self._fullscreenChanged("document"); });
        document.addEventListener("mozfullscreenchange", function(){ self._fullscreenChanged("mozfullscreenchange"); });
        document.addEventListener("webkitfullscreenchange", function(){ self._fullscreenChanged("webkitfullscreenchange"); });
        document.addEventListener("MSFullscreenChange", function(){ self._fullscreenChanged("MSFullscreenChange"); });

        if (options.spinner) {
            this.showSpinner("activate");
            this.player.addEventListener("loadedmetadata", function(){ self.hideSpinner("loadedmetadata"); });
            this.player.addEventListener("seeking", function(){ self.showSpinner("seeking"); });
            this.player.addEventListener("seeked", function(){ self.hideSpinner("seeked"); });
        }

        // // autoplayならパネル非表示
        if (options.autoplay)  {
            // self.hidePanel();
        }
        // 最初の停止中の表示
        else {
            self.showRewindAfterSpinner = true;
        }

        // loopならわざわざ表示する必要なし
        if (!options.loop)
            this.player.addEventListener("ended", function(){ self._showRewind(true, "ended"); });

        this.player.addEventListener("seeking", function(){ self._showRewind(false, "seeking"); });
        this.player.addEventListener("play", function(){ self._showRewind(false, "play"); });
        this.elements.rewind.addEventListener("click", function(){ self.player.rewindPlay(); });

        if (options.autoShow) {
            this.playerElement.addEventListener("mouseenter", function(){ self._showControlsPanel("mouseenter", true); });
            this.playerElement.addEventListener("mousemove", function(){ self._showControlsPanel("mousemove", true); });
            this.playerElement.addEventListener("mouseleave", function(){ if (!self.player.paused) self.hidePanel(); });
        }
        if (!options.volume) {
            this.elements.volume.style.display = "none";
        }
    }

    scale() {
        var self = this;
        var playerWidth = self.playerElement.clientWidth;

        // シークバーの長さがビデオ領域の半分以上あるべきという計算。
        // refs #11423 にあるようにIEではまともに計算できない。
        function widthBySeekbar() {
            var seekMinWidth = playerWidth * 0.5
            var seekWidth = self.elements.seekBar.clientWidth;

            // シークバーがプレイヤの半分を占めていればスケールしない。
            if (seekWidth >= seekMinWidth)
                return null;

            // placeHolderはサイズ0x0で、パネルの一番右にある。
            var placeRect = self.elements.placeHolder.getBoundingClientRect();

            // panelはwidth=100%なので、プレイヤと同じ幅になっているので、
            // placeHolderの右端がボタン類が現れている幅。
            var panelRect = self.elements.panel.getBoundingClientRect();
            var visibleWidth = placeRect.left + placeRect.width - panelRect.left;
            var expectedSeekWidth = visibleWidth * 0.5;

            // これだけを幅をパネルに設定して、スケールする。
            return expectedSeekWidth + visibleWidth;
        }

        // IEでは再生前や再生終了時に出てくる真ん中のでかいボタン RewindButton で計算する。
        // だが、rewindButtonは非表示の時はdisplay:noneにしているので、計算専用の .-movizo-center-holder を置いている。
        function widthByCenterHolder() {
            var playerHeight = self.playerElement.clientHeight;
            var centerHeight = self.elements.centerHolder.clientHeight;
            // ボタンの高さが画面の半分以下になるように。
            var expectedHeight = playerHeight / 2;
            if (centerHeight <= expectedHeight)
                return null;

            var scale = expectedHeight / centerHeight;
            return playerWidth / scale;
        }
        //var expectedWidth = this.isMSIE ? widthByCenterHolder() : widthBySeekbar();
        //
        // widthByRewindButton は IEの時の代替方法だったが、他でも実はそっちの方が出来が良いので
        // 同じ方法にした。
        var expectedWidth = widthByCenterHolder();
        if (expectedWidth == null)
            return;

        var scale = playerWidth / expectedWidth;

        this.fullStyles = {
            width: this.elements.panel.style.width,
            transform: null
        };

        this.scaledStyles = {
            width: `${expectedWidth}px`,
            transform: `scale(${scale})`
        };

        this._applyScaledStyle(this.scaledStyles);
    }

    _applyScaledStyle(styles) {
        this.elements.panel.style.width = styles.width;
        this.elements.panel.style.transform = styles.transform;
        this.elements.panel.style.transformOrigin = "left bottom";

        this.elements.spinner.style.transform = styles.transform;
        this.elements.rewind.style.transform = styles.transform;
    }

    // フルスクリーン状態が変わったというイベント
    _fullscreenChanged(targetElement) {
        if (this.scaledStyles) {
            if (this.isFullscreen) {
                this._applyScaledStyle(this.fullStyles);
            }
            else {
                this._applyScaledStyle(this.scaledStyles);
            }
        }
        this._showControlsPanel("_fullscreenChanged", true);
    }

    // フルスクリーン表示の切り替え
    toggleFullscreen() {
        function requestFullscreen(target) {
            if (target.webkitRequestFullscreen)
                target.webkitRequestFullscreen();
            else if (target.mozRequestFullScreen)
                target.mozRequestFullScreen();
            else if (target.msRequestFullscreen)
                target.msRequestFullscreen();
            else if (target.requestFullscreen)
                target.requestFullscreen();
            else {
                return false;
            }
            return true;
        }
        function exitFullscreen() {
            if (document.webkitCancelFullScreen)
                document.webkitCancelFullScreen();
            else if (document.mozCancelFullScreen)
                document.mozCancelFullScreen();
            else if (document.msExitFullscreen)
                document.msExitFullscreen();
            else if(document.cancelFullScreen)
                document.cancelFullScreen();
            else if(document.exitFullscreen)
                document.exitFullscreen();
            else
                return false;
            return true;
        }

        if (this.isFullscreen) {
            exitFullscreen();
            //this._showControlsPanel("toggleFullscreen/exitFullscreen", /*hideAfterwards*/true);
        }
        else {
            //if (requestFullscreen(this.player) || requestFullscreen(this.player.getCanvas()) ) {
            if (requestFullscreen(this.playerElement) || this.fullscreenElement && requestFullscreen(this.fullscreenElement) ) {
                // refs #11090: InlinePlayer/ フルスクリーン時にコントローラーが画面下にはみ出してしまう
                // Android で上記現象が発生する。原因は不明だけど display=block にしたら解消したので。
                this.playerElement.style.display = "block";
                //this._showControlsPanel("toggleFullscreen/requestFullscreen", /*hideAfterwards*/true);
            }
        }
    }

    get isFullscreen() {
        return ( document.fullscreenElement != null
            || document.webkitFullscreenElement != null
            || document.mozFullScreenElement != null
            || document.msFullscreenElement != null );
    }
    get canFullscreen() {
        return ( document.fullscreenEnabled
            || document.webkitFullscreenEnabled
            || document.mozFullScreenEnabled
            || document.msFullscreenEnabled );
    }

    _showControlsPanel(callFrom, hideAfterwards) {
        //console.warn(`_showControlsPanel: ${callFrom}, hideAfterwards=${hideAfterwards}`);
        this.showPanel();
        if (!this.player.paused && hideAfterwards) {
            this.delayHidePanel();
        }
    }

    // もう一回再生ボタン表示
    _showRewind(show, callFrom) {
        this.elements.rewind.style.display = show ? "block" : "none";
    }

    // スピナ表示
    showSpinner(callFrom) {
        //console.log(`showSpinner: from ${callFrom}`);
        this.elements.spinner.style.display = "block";
    }
    hideSpinner(callFrom) {
        //console.log(`hideSpinner: from ${callFrom}`);
        this.elements.spinner.style.display = "none";
        if (this.showRewindAfterSpinner) {
            this.showRewindAfterSpinner = false;
            this._showRewind(true, "hideSpinner");
        }
    }

    // ミュートボタン表示
    _visiblityMute(visiblity) {
        var display = visiblity ? "block" : "none";
        this.elements.mute.style.display = display;
    }

    _panelIsHidden() {
        return this.elements.panel.style.opacity == 0.0;
    }

    // パネル表示切り替え
    // @param  hideAfterwards  true = 表示後、自動的に非表示にする
    togglePanel(hideAfterwards) {
        if (this._panelIsHidden()) {
            this.showPanel();
            if (hideAfterwards)
                this.delayHidePanel();
        }
        else {
            this.hidePanel();
        }
    }

    // 必要な時にイベント登録しないと、opacity=0.0 で見えていない時でも、クリックできてしまう。
    _addMouseEvents() {
        var self = this;

        // なんでもない箇所をクリックすると、プレイヤ本体のクリックになるので、それを防ぐ
        // 例えば options.tapAction="mute" を指定して InlinePlayer を作った時に邪魔になる。
        this._addEventListener(this.elements.panel, "click", function(event){
            if (self._panelIsHidden()) return;

            // ただしシークバーは細いので、その上下をクリックしてもいいことにした。
            self._jumpSeek(event);
            event.stopPropagation();
        });

        this._addEventListener(this.elements.seekBar, "click", function(event){
            if (self._panelIsHidden()) return;
            self._jumpSeek(event);
            event.stopPropagation();
        });

        this._addEventListener(this.elements.play, "click", function(event){
            if (self._panelIsHidden()) return;

            if (self.player.paused) {
                self.player.pausedByUser = false;
                if (self.player.ended)
                    self.player.rewindPlay();
                else
                    self.player.play();
            }
            else {
                self.player.pausedByUser = true;
                self.player.pause();
            }
            event.stopPropagation();
        });

        this._addEventListener(this.elements.mute, "click", function(event){
            if (self._panelIsHidden()) return;

            self.player.muted = !self.player.muted;

            // ボリュームを下げていって、muted 状態になっていた時、
            // mute 解除により、音は半分にしておく。
            if (!self.player.muted && self.player.volume == 0)
                self.player.volume = 0.5;

            self._updateState("mute.click");
            event.stopPropagation();
        });

        this._addEventListener(this.elements.seekThumb, "mousedown", function(event){
            if (self._panelIsHidden()) return;

            self._startSeek(event, "mousemove", "mouseup");
            event.stopPropagation();
        });

        this._addEventListener(this.elements.seekThumb, "touchstart", function(event){
            if (self._panelIsHidden()) return;

            self._startSeek(event, "touchmove", "touchend", "touchcancel");
            event.stopPropagation();
        });

        this._addEventListener(this.elements.zoom, "click", function(event){
            if (self._panelIsHidden()) return;

            self.toggleFullscreen();
            event.stopPropagation();
        });

        this._addEventListener(this.elements.volume, "click", function(event){
            self._changeVolume(event, false);
            event.stopPropagation();
        });
        this._addEventListener(this.elements.volume, "mousedown", function(event){
            self._startChangeVolume(event, "mousemove", "mouseup");
            event.stopPropagation();
        });
        
        this._addEventListener(this.elements.skipBack, "click", function(event){
            self.player.currentTime -= 10;
            event.stopPropagation();
        });
        this._addEventListener(this.elements.skipFore, "click", function(event){
            self.player.currentTime += 10;
            event.stopPropagation();
        });
    }

    _addEventListener(elem, type, func) {
        if (!elem || !type || !func) return;

        elem.addEventListener(type, func);
    }

    _clearHideTimer() {
        if (this.hideTimer) {
            clearTimeout(this.hideTimer);
            this.hideTimer = null;
        }
    }

    showPanel() {
        this._clearHideTimer();
        this._updateState("showPanel");
        if (this._panelIsHidden()) {
            this.elements.panel.style.opacity = 1.0;
        }
    }

    hidePanel() {
        this._clearHideTimer();
        this.elements.panel.style.opacity = 0.0;
    }

    delayHidePanel(millisecond) {
        var self = this;
        millisecond = millisecond || 3000;
        if (millisecond < 1) {
            this.hidePanel();
        }
        else {
            this.hideTimer = setTimeout(function(){
                self.hideTimer = null;
                self.elements.panel.style.opacity = 0.0;
            }, millisecond);
        }
    }

    _appendDiv(classNames, parentElem) {
        var elem = document.createElement("div");
        if (Array.isArray(classNames)) {
            classNames.forEach(function(name){
                elem.classList.add(name);
            });
        }
        else {
            elem.classList.add(classNames);
        }
        parentElem.appendChild(elem);
        return elem;
    }

    _timeToS(time) {
        var second = parseInt(time);
        var minute = parseInt(second / 60);
        second -= minute * 60;

        second = "" + second;
        if (second.length < 2) {
            second = "0" + second;
        }

        return "" + minute + ":" + second;
    }

    _updateTime(elem, time, prefix) {
        var text = this._timeToS(time);
        if (prefix)
            text = prefix + text;
        elem.innerHTML = text;
    }

    _updateTimes(currentTime) {
        if (currentTime === undefined)
            currentTime = 0;

        // flashではちょっと再生しないと duration が取れないので、ここでは取れないね。
        // ということで、flashでは初期状態では duration は表示しなくてもいいことにした。
        var duration = this.player.duration;
        //if (duration == null || duration < 0.1 || Number.isNaN(duration))
        //この if は flash が 準備完了していない時 duration=null で返ってくること。
        // video タグが 準備完了していない時 NaN が返ってくること。
        // を想定したもの。
        if (duration > 0.1) {
            this._updateTime(this.elements.timeElapsed, currentTime);
            this._updateTime(this.elements.timeRemaining, duration - currentTime, "-");
        }
    }

    _updateProgress(currentTime) {
        this._updateTimes(currentTime);

        var percent = currentTime * 100.0 / this.player.duration;
        var style = "" + percent + "%";
        this.elements.seekThumb.style.left = style;
        this.elements.seekElapsed.style.width = style;
    }

    _onTimeupdate() {
        if (!this.thumbSeeking)
            this._updateProgress(this.player.currentTime);
    }

    _toggleClass(elem, value, trueClass, falseClass) {
        if (!elem) return;

        if (value) {
            elem.classList.add(trueClass);
            elem.classList.remove(falseClass);
        }
        else {
            elem.classList.add(falseClass);
            elem.classList.remove(trueClass);
        }
    }

    _clampVolumeIndex(volume) {
        if (volume < 0)
            volume = 0;
        if (volume > InlinePlayerControls._MAX_VOLUME)
            volume = InlinePlayerControls._MAX_VOLUME;
        return volume;
    }

    _updateVolume(refresh) {
        var volume = this.player.muted ? 0 : parseInt(this.player.volume * InlinePlayerControls._MAX_VOLUME);
        volume = this._clampVolumeIndex(volume);
        this.elements.volume.dataset.volume = volume;

        // refs #11423: IEではボリュームの表示反応が悪いので、こうするとする反映するようになった。
        if (this.isMSIE && refresh) {
            var self = this;
            this.elements.volume.style.opacity = 0.9;
            setTimeout(function(){ self.elements.volume.style.opacity = 1; }, 1);
        }

        this._toggleClass(this.elements.mute, this.player.muted, "-movizo-icon-mute", "-movizo-icon-unmute");
    }

    _updateState(callFrom) {
        //console.log(`updateState: from ${callFrom}`);
        var self = this;
        this._visiblityMute(this.player.hasAudio);
        this._toggleClass(this.elements.play, this.player.paused, "-movizo-icon-play", "-movizo-icon-pause");
        this._toggleClass(this.elements.zoom, this.isFullscreen, "-movizo-icon-unzoom", "-movizo-icon-zoom");
        this._updateVolume();
    }

    _jumpSeek(event) {
        var x = event.clientX - this.elements.seekBar.getBoundingClientRect().left;
        if (x >= 0 && x < this.elements.seekBar.clientWidth) {
            //var fraction = x / this.elements.seekBar.offsetWidth;
            var fraction = x / this.elements.seekBar.clientWidth;
            var seekPos = fraction * this.player.duration;
            this._updateProgress(seekPos);
            this.player.currentTime = seekPos;
        }
        event.preventDefault();
    }

    _startSeek(event, moveEvent, endEvent, cancelEvent) {
        this.thumbSeeking = true;
        // IE11では startsWith が無い
        var touch = moveEvent.startsWith ? moveEvent.startsWith("touch") : false;
        var self = this;
        var seekPos = this.player.currentTime;
        var initialThumbFraction = seekPos / this.player.duration;
        var initialThumbX = touch ? event.touches[0].pageX : event.clientX;

        function clamp(val) {
            if (val < 0) {
                return 0;
            } else if (val > 1) {
                return 1;
            } else {
                return val;
            }
        }

        function updateSeekPos(event) {
            var delta = (touch ? event.touches[0].pageX : event.clientX) - initialThumbX;
            var fraction = clamp(initialThumbFraction + delta / self.elements.seekBar.offsetWidth);
            seekPos = fraction * self.player.duration;
            self._updateProgress(seekPos);
            return seekPos;
        }

        var onMove = function(event) {
            updateSeekPos(event);
            event.preventDefault();
        };
        var onEnd = function(event) {
            self.thumbSeeking = false;
            self.player.currentTime = (touch ? seekPos : updateSeekPos(event));

            document.removeEventListener(moveEvent, onMove);
            document.removeEventListener(endEvent, onEnd);
            if (cancelEvent)
                document.removeEventListener(cancelEvent, onEnd);
            event.preventDefault();
        };

        document.addEventListener(moveEvent, onMove);
        document.addEventListener(endEvent, onEnd);
        if (cancelEvent)
            document.addEventListener(cancelEvent, onEnd);
        event.preventDefault();
    }

    _changeVolume(event, touch) {
        //var x = event.offsetX;
        //offsetX だとドラッグ中にボリューム要素外に出た時は、bodyの座標になる。
        var x = event.clientX - this.elements.volume.getBoundingClientRect().left;

        // ８ステップあるが、最大にしょうとすると、ボリューム領域より右をクリックしないと
        // いけないので、７段階で判定する。
        var w = this.elements.volume.clientWidth * 7 / 8;

        function clamp(val) {
            if (val < 0) {
                return 0;
            } else if (val > 1) {
                return 1;
            } else {
                return val;
            }
        }

        var volume = clamp(x / w); // 0.0 .. 1.0
        this.player.muted = (volume == 0);
        this.player.volume = volume;

        // IEではボリュームクリックしてもすぐに反応しないので、player にvolumeを設定する前に
        // this._updateVolume(volume) で、やってみたが、やっぱり遅い。
        // おそらく css で [data-volume=8] とかのセレクタの反応が遅いのでしょう。
        this._updateVolume(/*refresh*/true);
    }

    _startChangeVolume(event, moveEvent, endEvent, cancelEvent) {
        this.volumeChanging = true;
        // IE11では startsWith が無い
        var touch = moveEvent.startsWith ? moveEvent.startsWith("touch") : false;
        var self = this;
        self._changeVolume(event, touch);

        var onMove = function(event) {
            if (self.volumeChanging) {
                self._changeVolume(event, touch);
                event.preventDefault();
            }
        };
        var onEnd = function(event) {
            self.volumeChanging = false;
            document.removeEventListener(moveEvent, onMove);
            document.removeEventListener(endEvent, onEnd);
            if (cancelEvent)
                document.removeEventListener(cancelEvent, onEnd);
            event.preventDefault();
        };

        document.addEventListener(moveEvent, onMove);
        document.addEventListener(endEvent, onEnd);
        if (cancelEvent)
            document.addEventListener(cancelEvent, onEnd);
        event.preventDefault();
    }
}

InlinePlayerControls._styleSheetAdded = false;
InlinePlayerControls._MAX_VOLUME = 8;

export default InlinePlayerControls;
