export class Logger {

    constructor(enabled = false) {
        this.enabled = enabled
    }

    log(message, data) {
        if (this.enabled) console.log(message, data)
    }

    error(message, data) {
        if (this.enabled) console.error(message, data)
    }

    warn(message, data) {
        if (this.enabled) console.warn(message, data)
    }

}